<template>
	<view class="register">
		<u-row
			style="background-color: #1F61BE;margin: 0 -30rpx;padding: 30rpx 30rpx 90rpx 30rpx;border-radius: 0px 0px 10rpx 10rpx;">
			<u-col span="9">
				<view style="font-size: 36rpx;font-weight: bold;color: #fff;">{{details.universalAntiAboutMain26}}
				</view>
				<view style="font-size: 24rpx;margin: 14rpx 0 20rpx 0;color: #B1D1FF;">{{details.createTime}}</view>
			</u-col>
			<u-col span="3" style="text-align: right;" v-show="baomin!=1">
				<u-button size="mini" class="btn" shape="circle" @click="moshow=true">报名</u-button>
			</u-col>
		</u-row>
				<view style="width:100%;margin-top:20rpx;position: relative;">
					<image style="border-radius: 18rpx;width: 100%;" src="/static/img/banner.png" mode="widthFix"></image>
					<view class="bmz">{{ztcon(details.aboutEnterStatus)}}</view>
				</view>
			<u-row style="background-color: #fff;padding: 0 30rpx 20rpx 30rpx;margin: 24rpx -30rpx;">
				<u-col span="12" class="txtbox">
					<p>招募人数：<span>{{details.nowNumberPeople}}/{{details.numberPeople}}</span></p>
					<p>活动时间：<span>{{details.startDate}}</span></p>
					<p>活动地址：<span>{{details.address}}</span></p>
					<p>活动标语：<span>{{details.title}}</span></p>
					<p>主办单位：<span>{{fycon(details.deptId)}}</span></p>
					<p>联系人：<span>{{details.contacts}}</span></p>
					<p>联系电话：<span>{{details.contactsPhone}}</span></p>
					<p>用餐安排：<span>{{details.mealArrangement}}</span></p>
					<p>住宿安排：<span>{{details.accommodationArrangements}}</span></p>
					<p>交通安排：<span>{{details.trafficArrangement}}</span></p>
				</u-col>
				<u-col span="12" class="jjbox">
					<p>简介</p>
					<view class="jtxt">
						{{details.activityContent}}
					</view>
				</u-col>
			</u-row>
		<u-select title="请选择分组" v-model="moshow" :list="list01" value-name='id' label-name='activitiyName'
			@confirm="sushe_confirm"></u-select>
		<u-toast ref="uToast"></u-toast>
	</view>
</template>

<script>
	import {
		gethdInfo,
		listDept,
		listpepoleObj,
		assignTasks,
		getTasks,
		assignGroup,
		addassignGroup,
		getDicts,
		getinfoList
	} from '../../api/index.js'
	export default {
		data() {
			return {
				form: {},
				details: {},
				params: {},
				current: 0,
				list01: [],
				list02: [{
					name: '反恐防恐小常识',
					time: '3天前',
					pic: '/static/img/banner.png',
					status: 1,
				}, {
					name: '嫌疑人',
					time: '3天前',
					pic: '/static/img/banner.png',
					status: 2,
				}, {
					name: '反恐',
					time: '3天前',
					pic: '/static/img/banner.png',
					status: 1,
				}, ],
				list: [{
					name: '活动详情'
				}, {
					name: '报名信息'
				}],
				bmArr: [],
				moshow: false,
				show: false,
				ztArr:[],
				baomin:null,
			}
		},
		onLoad() {
			listDept().then(res => { //志愿者分队
				if (res.code == 200) {
					this.bmArr = res.data;
				}
			})
			getDicts('work_enter_status').then(res => { //系统是否
				if (res.code == 200) {
					this.ztArr = res.data;
					//this.sushe_show = true;
				}
			})
			this.baomin = this.$route.query.baomin;
			//console.log("this.$route.query.baomin",this.$route.query.baomin);
			if(this.$route.query.id){
				getinfoList(this.$route.query.id).then(res => {
				if (res.code == 200) {
					this.details = res.data;
					let data={};
					data.queryJson = [{
						data1: 'fid',
						data2: '0',
						data3: this.details.id,
						objectId: "298",
						subType: null,
						flag: false,
						fileType: "String"
					}];
					data.queryJson = JSON.stringify(data.queryJson);
					getTasks(data).then(res => {
						if (res.code == 200) {
							this.list01 = res.rows;
						}
					})
				}
			})
			}else {
				this.params.procInstId = this.$route.query.procInstId;
            
			gethdInfo(this.params).then(res => {
				if (res.code == 200) {
					this.details = res.data;
					let data={};
					data.queryJson = [{
						data1: 'fid',
						data2: '0',
						data3: this.details.id,
						objectId: "298",
						subType: null,
						flag: false,
						fileType: "String"
					}];
					data.queryJson = JSON.stringify(data.queryJson);
					getTasks(data).then(res => {
						if (res.code == 200) {
							this.list01 = res.data;
						}
					})
				}
			})
			}
			
		},
		methods: {
			ztcon(name){
				let result = null;
				this.ztArr.map((item) => {
					if (item.dictValue == name) {
						result = item.dictLabel;
					}
				})
				return result;
			},
			sushe_confirm(e) {
				addassignGroup({
					universalAntiAboutFid: this.details.id,
					universalAntiAboutWorkFid: e[0].value
				}).then(res => {
					if (res.code == 200) {
						this.$refs.uToast.show({
							title: '报名成功'
						});
						this.moshow = false;
					}
				})
			},
			totask(id) {
				this.form.id = id;
				this.show = true;
			},
			people() {
				listpepoleObj({
					universalAntiAboutFid: this.details.id
				}).then(res => {
					if (res.code == 200) {
						this.list02 = res.data;
					}
				})
			},
			fycon(name) {
				let result = null;
				this.bmArr.map((item) => {
					if (item.deptId == name) {
						result = item.deptName;
					}
				})
				return result;
			},
		}
	}
</script>

<style scoped>
	.register {
		background-color: #F5F6FA;
		padding: 0 30rpx;
		width: 100vw;
		height: 100vh;
	}

	.popupCon0 {
		padding: 33upx 30upx 40upx;
	}

	.popupBtn0 {
		display: flex;
		align-items: center;
		justify-content: space-between;
		font-size: 34upx;
		font-family: Source Han Sans CN;
		font-weight: 400;
		color: #028CFE;
		line-height: 34upx;
		margin-bottom: 34upx;
	}

	.close-img {
		width: 38rpx;
		height: 38rpx;
		color: #999;
	}


	.register /deep/ .u-tabs {
		background: transparent !important;
	}

	.btn {
		border: 2rpx solid #FFFFFF;
		background: transparent;
		color: #fff;
		width: 120rpx;
	}

	.bmz {
		position: absolute;
		top: 0;
		left: 0;
		width: 161rpx;
		height: 48rpx;
		line-height: 48rpx;
		font-size: 28rpx;
		color: #FFFFFF;
		background: #FB8333;
		box-shadow: 0px 3rpx 6rpx 1rpx rgba(255, 255, 255, 0.16);
		border-radius: 10rpx 0px 10rpx 0px;
	}

	.txtbox {
		margin: 24rpx 0 43rpx 0;
	}

	.txtbox p {
		margin-bottom: 10rpx;
		color: #999;
	}

	.txtbox span {
		color: #333;
	}

	.jjbox {
		padding-bottom: 30rpx;
	}

	.jjbox p {
		margin-bottom: 16rpx;
	}

	.jjbox .jtxt {
		background-color: #F9F9FB;
		padding: 20rpx 18rpx;
		color: #B1B1B1;
		border-radius: 4rpx;
	}

	.box {
		background-color: #fff;
		margin: 24rpx -30rpx;
	}

	.mititle {
		font-size: 28rpx;
		font-weight: bold;
		color: #333333;
		height: 90rpx;
		line-height: 90rpx;
		padding: 0 30rpx;
		border-bottom: 1px solid #f1f1f1;
	}

	.list02 span {
		font-size: 28rpx;
		font-weight: bold;
		color: #333333;
	}

	.list02 p {
		margin-top: 20rpx;
		font-size: 24rpx;
		color: #999999;
		width: 90%;
		/*宽度记得设置，这样超出宽度的内容才会被省略*/
		overflow: hidden;
		/*文本超出隐藏*/
		white-space: nowrap;
		text-overflow: ellipsis;
		/*文本超出省略*/
	}

	.list02 b {
		font-size: 24rpx;
		color: #4490FB;
		font-weight: normal;
	}

	.tiptxt {
		position: absolute;
		right: 60rpx;
		top: 60rpx;
	}

	.tiptxt01 {
		position: absolute;
		right: 148rpx;
		top: 60rpx;
	}

	.list02 {
		width: 100%;
		height: 180rpx;
		margin: 26rpx 30rpx;
		position: relative;
		font-size: 24rpx;
		color: #999;
		border-bottom: 1rpx solid #EEEEEE;
	}
</style>